.markdown {
  position: relative;
}
.markdown .md-paragraph {
  font-size: 14px;
  color: #45454d;
}
.markdown .md-listitem {
  font-size: 14px;
  color: #45454d;
  margin: 4px 0 0 20px;
  list-style-type: circle;
  padding-left: 4px;
}
.markdown h1.md-heading {
  color: #0d1a26;
  font-weight: 500;
  margin-bottom: 20px;
  margin-top: 8px;
  font-family: Lato, Monospaced Number, Chinese Quote, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 32px;
  line-height: 38px;
}
.markdown h2.md-heading {
  font-size: 24px;
  line-height: 45px;
}
.markdown h2.md-heading,
.markdown h3.md-heading,
.markdown h4.md-heading,
.markdown h5.md-heading,
.markdown h6.md-heading {
  color: #18181a;
  font-family: Lato, Monospaced Number, Chinese Quote, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Helvetica Neue, Helvetica, Arial, sans-serif;
  margin: 1.6em 0 0.6em;
  clear: both;
}
.markdown h3.md-heading {
  font-size: 18px;
}
.markdown h4.md-heading {
  font-size: 14px;
}
.markdown h5.md-heading {
  font-size: 12px;
}
.markdown h6.md-heading {
  font-size: 10px;
}
.markdown img {
  max-width: 100%;
}
.markdown .meta {
  color: #45454d;
}
.markdown .meta .description p {
  white-space: pre-wrap;
}
.markdown .meta p > img {
  margin: 34px 0;
  -webkit-box-shadow: 0 8px 20px rgba(143, 168, 191, 0.35);
  box-shadow: 0 8px 20px rgba(143, 168, 191, 0.35);
}
.markdown .meta hr {
  height: 1px;
  border: 0;
  background: #ebedf0;
  margin: 72px 0;
  clear: both;
}
.markdown .meta pre {
  border-radius: 2px;
}
.markdown .meta pre code {
  border: none;
  margin: 0;
  font-size: 13px;
  overflow: auto;
}
.markdown .meta p code {
  border: none;
  margin: 0;
  font-size: 13px;
  overflow: auto;
}
.markdown .meta pre span {
  border: none;
  margin: 0;
  padding: 0;
  font-size: 13px;
  overflow: auto;
  font-family: Menlo, Monaco, Consolas, Courier, monospace;
}
.markdown .meta b,
.markdown .meta strong {
  font-weight: 500;
}
.markdown .meta pre span.cm-variable,
.markdown .meta pre span.cm-tag {
  color: #f81d22;
}
.markdown .meta pre span.cm-tag.cm-bracket {
  color: #999;
}
.markdown .meta pre span.cm-keyword,
.markdown .meta pre span.cm-string {
  color: #008dff;
}
.markdown .meta pre span.cm-attribute {
  color: #0b8235;
}
.markdown .meta pre span.cm-def,
.markdown .meta pre span.cm-property {
  color: #45454d;
}
.markdown .meta .CodeMirror-line {
  display: block;
}
.markdown table.grid {
  border-collapse: collapse;
  width: 100%;
  background-color: #fff;
  color: #5e6d82;
  font-size: 14px;
  margin-bottom: 45px;
}
.markdown table.grid strong {
  font-weight: normal;
}
.markdown table.grid th {
  text-align: left;
  border-top: 1px solid #eaeefb;
  background-color: #fafafa;
  white-space: nowrap;
}
.markdown table.grid td,
.markdown table.grid th {
  word-break: break-all;
  border: 1px solid #e6e6eb;
  padding: 10px;
  max-width: 250px;
}
.markdown table.grid th:first-child,
.markdown table.grid td:first-child {
  padding-left: 10px;
}
.markdown code {
  margin: 0 1px;
  background: #f2f4f5;
  padding: 0.2em 0.4em;
  border-radius: 3px;
  font-size: 0.9em;
  border: 1px solid #eee;
}
.markdown > div > div > div:not(.demo-container) ul > li {
  margin: 4px 0 0 20px;
  padding-left: 4px;
}
.markdown > div > div > div:not(.demo-container) ul > li code {
  margin: 0 4px;
}
.markdown > div > div > div:not(.demo-container) ol > li {
  margin: 4px 0 0 20px;
  list-style-type: decimal;
  padding-left: 4px;
}
.markdown > div > div > div:not(.demo-container) ol > li code {
  margin: 0 4px;
}
.markdown > div > div > div:not(.demo-container) .icon-list > li {
  list-style: none;
  padding: 0;
  margin: 0;
}
.markdown > div > div > div:not(.demo-container) ul > li:empty {
  display: none;
}
.markdown > div > div > div:not(.demo-container) ol > li > p,
.markdown > div > div > div:not(.demo-container) ul > li > p {
  margin: 0.2em 0;
}
.markdown > div > div > div:not(.demo-container) img {
  max-width: calc(100% - 32px);
}
.md-owner {
  display: inline-block;
  position: relative;
  font-weight: normal;
  margin-left: 8px;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.md-owner .owner-title {
  font-size: 12px;
  cursor: default;
}
.md-owner .owner-list {
  display: none;
  list-style: none;
  position: absolute;
  border-radius: 2px;
  min-width: 100px;
  width: max-content;
  left: 25px;
  bottom: -10px;
  font-size: 14px;
  padding: 12px;
  color: #333;
  background: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}
.md-owner .owner-list:after {
  left: -5px;
  bottom: 12px;
  width: 9px;
  height: 9px;
  background: #fff;
  box-shadow: -1px 1px 4px rgba(0, 0, 0, 0.05);
  transform: rotate(45deg);
  position: absolute;
  content: '';
}
.md-owner .owner-list .item {
  height: 12px;
  line-height: 12px;
  font-size: 12px;
}
.md-owner .owner-list .item:not(:last-child) {
  margin-bottom: 8px;
}
.md-owner:hover .owner-title {
  color: #337eff;
}
.md-owner:hover .owner-list {
  display: block;
}
code[class*=language-],
pre[class*=language-] {
  color: #000;
  background: #f9fafc;
  direction: ltr;
  text-align: left;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}
.browser-mockup {
  border-top: 2em solid rgba(230, 230, 230, 0.7);
  -webkit-box-shadow: 0 0.1em 0.5em 0 rgba(0, 0, 0, 0.28);
  box-shadow: 0 0.1em 0.5em 0 rgba(0, 0, 0, 0.28);
  position: relative;
  border-radius: 3px 3px 0 0;
}
.browser-mockup:before {
  display: block;
  position: absolute;
  content: '';
  top: -1.25em;
  left: 1em;
  width: 0.5em;
  height: 0.5em;
  border-radius: 50%;
  background-color: #f44;
  -webkit-box-shadow: 0 0 0 2px #f44, 1.5em 0 0 2px #9b3, 3em 0 0 2px #fb5;
  box-shadow: 0 0 0 2px #f44, 1.5em 0 0 2px #9b3, 3em 0 0 2px #fb5;
}
.browser-mockup:after {
  display: block;
  position: absolute;
  content: '';
  top: -1.6em;
  left: 5.5em;
  width: calc(100% - 6em);
  height: 1.2em;
  border-radius: 2px;
  background-color: white;
}
.browser-mockup iframe {
  width: 100%;
  border: 0;
}
